@import url("https://fonts.googleapis.com/css2?family=Alegreya:wght@500&family=El+Messiri&family=Josefin+Slab:wght@100&display=swap");

/*
font-family: 'Alegreya', serif; // titulos mas generico
font-family: 'El Messiri', sans-serif; // subtitulos
font-family: 'Josefin Slab', serif; // parrafos
*/

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "El Messiri", sans-serif;
}

body {
  background-color: #333;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 10px rgba(59, 59, 59, 0.5);
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: #8033b9;
}
